import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import CommonData from "../shared/CommonData";

import { Box, Grid, Typography } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  hrStyle: {
    marginLeft: 0,
  },
  large: {
    width: "80%",
    height: "auto",
  },
}));

export default () => {
  const classes = useStyles();

  return (
    <div>
      <Grid container m={2} justify="space-between" >
        <Grid item xs={6}>
          <Typography variant="h4" component="h4">
            {CommonData.name}
          </Typography>
          <Typography variant="h6" component="h6">
            {CommonData.designation}
          </Typography>
        </Grid>
        <Grid item xs={6}>
          <Grid container direction="row-reverse">
            <Avatar
              alt="Aklesh Sakunia"
              width={200}
              src="aklesh-cropped-min.jpg"
            />
          </Grid>
          <Grid container direction="row-reverse">
            <span>{CommonData.email}</span>
          </Grid>
          <Grid container direction="row-reverse">
            <span>{CommonData.phoneNumber}</span>
          </Grid>
          <Grid container direction="row-reverse">
            <span>{CommonData.location}</span>
          </Grid>
        </Grid>
        <Grid item>
          <hr className={classes.hrStyle} width="60%" size={2} color="#fff" />

          <p>{CommonData.motto}</p>
        </Grid>
      </Grid>
    </div>
  );
};
